<template>
    <div class="xgMobilewrap">
      <div class="xgMobile">
        <div class="mobile">
          <span>手机号</span>
          <input type="number" v-model="mobile" placeholder="输入手机号">
        </div>
        <div class="saf_wrap">
          <div class="safe_info">校验码</div>
          <input type="number" v-model="code" placeholder="短信验证码">
          <div :class="{getCode:true,sendCode:!isSend}" @click="getCode()">{{codeInfo}}</div>
        </div>
      </div>
      <van-button type="default btn_cz" @click="toChange()">确认</van-button>
       <div class="bottom_pass">
        <span @click="()=>{this.$router.go(-1)}" class="iconfont icon-shangyiye" ></span>
        <span :class="activeNum>=3?'':'disabled'" @click="()=>{if(activeNum>=3){this.$router.go(1)}}" class="iconfont icon-xiayiye" ></span>
      </div>
    </div>
</template>

<script>
  import {regMobile } from "@/common/js/Reg";
import {apiGet} from "@/common/js/api";
import {apiPost} from "@/common/js/api";
  import { Toast } from 'vant';
import { getStore,setStore } from '../assets/utils';
  export default {
        name: "xgMobile",
      data(){
          return {
            isSend:true,
            codeInfo:'获取验证码',
            isLoading: false,
            code:'',
            mobile:'',
            codeback:'',
            accessToken:'',
            activeNum:""
          }
      },
      created(){
        this.accessToken=this.$cookie.get('accessToken');
        this.activeNum = getStore('activeNum')
        console.log(this.accessToken);
      },
      methods:{
        onRefresh() {
          setTimeout(() => {
            this.$toast('刷新成功');
            this.isLoading = false;
            this.count++;
          }, 500);
        },
        getCode() {
          if(regMobile.test(this.mobile) && this.isSend){
            apiGet('/account/sms',{cellphone: this.mobile,type:4}).then((res)=>{
              console.log(res)
              if(res.status==200){
                this.codeback=res.data.data.phonecode;
                console.log(this.codeback)
                Toast(res.data.message);
                if (!this.isSend) {
                  return
                }
                this.isSend = false
                let num = 60
                this.codeInfo=`(${num}秒后重试)`
                this.timer = setInterval(() => {
                  num --;
                  this.codeInfo=`(${num}秒后重试)`
                  if (num == 0) {
                    clearInterval(this.timer)
                    this.codeInfo=`获取验证码`
                    this.isSend = true
                  }
                }, 1000)
              }else {
                Toast('验证码发送失败,请重试');
                clearInterval(this.timer)
                this.codeInfo=`获取验证码`
                this.isSend = true
              }
            })
          }else if(this.codeInfo != '获取验证码'){
            Toast('验证码发送频繁,请稍后再试');
          }
          else{
            Toast('请输入正确格式的手机号!');
          }
          /*axios.get('/api/menu',{params:{restaurant_id: 155147,terminal:'web'}}).then((res)=>{
            console.log(res)
          })*/
        },
        toChange(){
          if(this.codeback==this.code){
            if(regMobile.test(this.mobile)){
              apiPost('/account/edit-phone',{accessToken:this.accessToken,cellphone:this.mobile,code:this.code}).then((res)=>{
                console.log(res)
                Toast(res.data.message);
                if(res.data.data.cellphone){
                  this.$cookie.set('cellphone',res.data.data.cellphone, { expires: '1Y' })
                  setStore('activeNum',3)
                  this.$router.go(-2)
                }else {
                  clearInterval(this.timer)
                  this.codeInfo=`获取验证码`
                  this.isSend = true
                }
              })
            }
          }else {
            Toast('验证码错误!');
          }
        }
      }
    }
</script>

<style scoped lang="scss">

  .xgMobilewrap{
    width: 100%;
    height: 100%;
    padding-top: 0.12rem;
    background: #E6E9ED;
    margin: auto;
    .xgMobile{
      width:3.5rem;
      height:1.02rem;
      background:rgba(255,255,255,1);
      border-radius:0.04rem;
      margin: 0.18rem auto;
      .mobile{
        height: 0.56rem;
        width: 3.5rem;
        line-height: 0.56rem;
        span{
          font-size: 0.17rem;
          color: #333;
          width: 20%;
          float: left;
          text-align: center;
        }
        input{
          font-size: 0.15rem;
          width: 70%;
          height: 0.5rem;
          border: none;
          margin-left: 0.15rem;
          color: #9a9a9a;
        }
      }
      .saf_wrap{
        margin: 0 auto;
        width: 3.5rem;
        height: 0.56rem;
        background: white;
        border-radius: 0.04rem;
        display: flex;
        .safe_info,.getCode{line-height: 0.5rem;text-align: center;height: .5rem}
        .safe_info{color: #333;font-size: 0.17rem;width:20%}
        .getCode{font-size: .14rem;color: #3C7FEE;width: 25%;position: relative;}
        .sendCode{color: #bcbcbc}
        .getCode::before{content: '';width: 1px;height: 0.22rem;background: #f5f5f5;position: absolute;top:0.14rem;left: 0.015rem;}
        input{width: 55%;border: none;text-align: left;height: 0.5rem;line-height: .5rem;padding-left: 0.15rem;font-size: 0.15rem}
      }
    }
    .btn_cz{
      width:3.06rem;
      height:0.47rem;
      background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
      border-radius:0.04rem;
      color: white;
      display: block;
      font-size: 0.19rem;
      margin:0.16rem auto 0;
      margin-top: 1.5rem;
    }
  }

</style>



